body ::-webkit-scrollbar {
  position: absolute;
  width: 12px;
  margin-left: -10px;
  -webkit-appearance: none; }

body ::-webkit-scrollbar-track {
  background-color: transparent;
  border-right: none;
  border-left: none; }

body ::-webkit-scrollbar-thumb {
  height: 50px;
  background-color: rgba(160, 174, 186, 0.2);
  background-clip: content-box;
  border-color: rgba(0, 0, 0, 0);
  border-style: solid;
  border-width: 1px 2px;
  border-radius: 7px; }

legend {
  font-size: 1rem;
  color: #5e6773; }

footer {
  bottom: 0;
  right: 0;
  padding-top: 15px;
  padding-bottom: 10px;
  background-color: #F5F6F9;
  color: #a0aeba; }
  @media screen and (max-width: 767px) {
    footer {
      position: relative; } }

.copyright {
  margin-bottom: 0;
  margin-right: 10px;
  text-align: right;
  font-size: 13px; }
  @media screen and (max-width: 767px) {
    .copyright {
      width: 100%;
      text-align: center;
      position: relative !important; } }

.separator-linethrough {
  position: relative;
  margin: 30px 0;
  text-align: center; }
  .separator-linethrough span {
    display: inline-block;
    padding: 0 10px;
    position: inherit;
    font-size: 18px;
    background-color: #fff;
    z-index: 9; }
  .separator-linethrough:after {
    display: block;
    position: absolute;
    top: 50%;
    width: 100%;
    content: '';
    border-top: 1px solid #f0f0f0;
    z-index: 0; }

