  .wizard:after {
    clear: both; }
  .wizard ul {
    display: flex;
    width: 4000px;
    padding: 0;
    margin: 0;
    list-style: none outside none; }
    .wizard ul.previous-disabled li.complete {
      cursor: default; }
      .wizard ul.previous-disabled li.complete:hover {
        color: #468847;
        cursor: default;
        background: #f3f4f5; }
        .wizard ul.previous-disabled li.complete:hover .chevron:before {
          border-left-color: #f3f4f5; }
    .wizard ul li {
