        .wizard ul li.complete:hover {
          cursor: pointer; }
          .wizard ul li.complete:hover .chevron:before {
            border-left: 14px solid #41B314; }
        .wizard ul li.complete .chevron {
          border-left-color: #fff; }
          .wizard ul li.complete .chevron:before {
            border-left: 14px solid #41B314; }
      .wizard ul li.active {
